body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

form {
  margin: 2em 2em;
  height: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.icon {
  transition: ease-in-out .2s;
}
.icon:hover {
  background-color: orange;
}

form h2 {
  margin: 0px;
  font-style: italic;
  font-weight: lighter;
  text-align: center;
}

.form-options {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
  width: 70vw;
  border-radius: 15px;
  padding: 5px;
  border: solid 2px;
  cursor: pointer;
}

.form-options p {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: solid 1px #282c34;
  padding: 9px;
}

.form-options p:hover {
  border: solid 1px orange;
  padding: 9px;
}

.form-subheader {
  margin: 0 0 0 2vw;
  align-self: start;
}

button a {
  text-decoration: none;
  color: black;
  font-weight: bold;
}

.button-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.choice {
  border: solid 2px #282c34;
  padding: 9px;
  border-radius: 15px;
}


p svg {
  width: 6vw;
  height: 10vh;
}

.selected-choice {
  background-color: orange;
  color: #282c34;
  padding: 9px;
  border-radius: 15px;
}

.submit-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  text-decoration: none;
  width: 94%;
}

.submit-link:hover {
  color: #282c34;
}

