  outline: none;
}

.disabled-link {
  pointer-events: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  text-decoration: none;
  width: 94%;
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
  width: auto;
}

.app-name {
  font-size: 1.5em;
  font-style: italic;
  margin-left: -10vw;
  z-index: 1;
}

.container-title {
    margin: 1em;
    align-self: center;
}

.song-result {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: solid 3px white;
    border-radius: 15px;
    padding: clamp(.8em, 2vh, 3em) clamp(1em, 2vw, 2em);
    transition: all 0.2s ease-in-out;
    margin-bottom: 1em;
    width: clamp(15em, 60vw, 55em);
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
}

.song-details {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    padding: 0em clamp(1em, 2vw, 2em);
}

.title-artist {
    flex: 2 1;
    line-height: 1.2em;
}

h1 {
    line-height: normal;
    margin: 0;
}

h3 {
    font-weight:300;
    margin: 0;
    font-size: .6em;
}

.date-genre {
    display: flex;
    /* padding: 0em clamp(1em, 2vw, 2em); */
    font-size: .5em;
}

.date-genre .badge {
    border: solid 1.5px white;
    border-radius: 15px;
    padding: 0px 8px;
    /* line-height: 0px; */
    background-color: white;
    color: rgb(40,44,52);
    font-size: 1.2em;
    font-weight: 300;
    margin-right: 1em;
}

.favoriteBtn {
    font-size: clamp(1em, 10vw, 2.1em);
    background: none;
    border: none;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    cursor: pointer;
    transition: ease-in .1s;
    margin-bottom: 3px;
}

.favoriteBtn:focus {
    outline: none
}

.favoriteBtn:hover{
    -webkit-transform: rotate(25deg);
            transform: rotate(25deg);
}

.button-container {
    display: flex;
    /* justify-content: space-between;
    flex:.3; */
    min-width: auto;
}

.btn {
    padding: 0 !important;
}

.spotify-button {
    background-color: #0000ff00;
    border: none;
    width: clamp(3em, 10vw, 4.2em);
    cursor: pointer;
    transition: ease-in-out .2s;
    margin-right: 2.4em;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.spotify-button:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.spotify-button:focus {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    outline: none;
}

.spotify-button .spotify-svg path {
    fill: rgb(29,208,92);
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%)
}

.favorite {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: solid 3px rgb(253,235,103);
    border-radius: 15px;
    padding: clamp(.8em, 2vh, 3em) clamp(1em, 2vw, 2em);
    transition: all 0.2s ease-in-out;
    margin-bottom: 1em;
    width: clamp(15em, 60vw, 55em);
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
}

.favorite:hover {
  background: orange;
  color: #364259;
}

.remove-fav-btn {
  font-size: clamp(.5em, .75em, 1.2em);
  font-weight: lighter;
  color: #282c34;
  background: none;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  transition: ease-in .1s;
  margin-bottom: .5vh;
  height: 50%;
}

.remove-fav-btn:hover {
  color: white;
  background: #282c34;
  border: 2px solid white;
}

.remove-fav-btn:focus {
  outline: none
}

.fav-details {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
  padding: 0em clamp(1em, 2vw, 2em);
}

.fav-title-artist {
  flex: 2 1;
  line-height: 1.2em;
  color: rgb(253,235,103);
}

.fav-title-artist h1 {
  line-height: normal;
  margin: 0;
}

.fav-title-artist h3 {
  font-weight:300;
  margin: 0;
  font-size: .6em;
}

.fav-date-genre {
  display: flex;
  font-size: .5em;
}

.fav-date-genre .fav-badge {
  border: solid 1.5px rgb(253,235,103);
  border-radius: 15px;
  padding: 0px 8px;
  background-color: rgb(253,235,103);
  color: rgb(40,44,52);
  font-size: 1.2em;
  font-weight: 300;
  margin-right: 1em;
}

.fav-button-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex:.3 1;
}

.favorites-view {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgb(255, 255, 255);
}

.favorites-header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 60vw;
}

.back-arrow {
  margin-right: 20vw;
}

.back-arrow svg path:hover {
    fill: #e100a9;
}

.back-arrow svg {
  width: 2.5vw;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

