  .favorites-view h2 {
  margin-right: 18vw;
  }
} */

.favorites-view h2 {
  padding-right: 22vw;
  font-weight: 200;
}

.favs-container {
  display: flex;
  width: 80vw;
  flex-direction: column;
  align-items: center;
}

.no-favorites {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.music-note {
  display: flex;
  flex-direction: row;
  top: .5vh;
  left: 4.2em;
  position: absolute;
  -webkit-animation: linear initial alternate;
          animation: linear initial alternate;
  -webkit-animation-name: run;
          animation-name: run;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  width: 4em;
  height: 3.6em;
  opacity: 100%;
}

.link-container {
  display: flex;
  width:-webkit-fit-content;
  width:-moz-fit-content;
  width:fit-content;
}

.nav-btn {
  flex:1 1;
  padding: .2em .4em;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: clamp(13px, 1em, 2em);
  font-weight: 300;
  border: 1.4px solid white;
  border-radius: 10px;
  transition: ease-in-out .2s;
  margin: 0 .6em;
}

.nav-btn-link {
  color: white;
  width: 45%;
  font-weight: bolder;
  text-decoration: none;
}

.nav-btn:hover {
  background: #e100a9;
  color: #282c34;
  border-color: #e100a9;
}

.app-header {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 1vw 0;
  border-bottom: 2px solid white;
  background-color: #282c34;
}

.app-name-link h1 {
  font-size: 2em;
  font-style: italic;
  font-weight: 300;
}

.app-name {
}

.app-name-link {
  text-decoration: none;
  color: white;
  z-index: 1;
}

